import { TablePagination } from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import { setRowsPerPage } from '../../state/slices/pagination.slice';
import { useAppDispatch, useAppSelector } from '../../state/store';

interface IPagination {
    count: number;
    pageNum: number;
}

export const Pagination: React.FC<IPagination> = ({ count, pageNum }) => {
    const rowsPerPage = useAppSelector((state) => state.pagination.rowsPerPage);
    const dispatch = useAppDispatch();
    const history = useHistory();

    const handleChangeRowsPerPage = (
        event: React.ChangeEvent<HTMLInputElement>
    ) => {
        dispatch(setRowsPerPage(parseInt(event.target.value, 10)));
    };

    const handlePageChange = (
        event: React.MouseEvent<HTMLInputElement>,
        lastPage: number
    ) => {
        const nextPage = lastPage + 1;
        history.push(`/books/${nextPage}`);
    };

    if (!count) {
        return null;
    }

    return (
        <TablePagination
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            count={count}
            rowsPerPage={rowsPerPage}
            page={pageNum === 0 ? 1 : pageNum - 1}
            onChangePage={handlePageChange}
            onChangeRowsPerPage={handleChangeRowsPerPage}
        />
    );
};
