import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import { IBooks } from '../../state/api-slices/books.api.slice';

export const BooksTable: React.FC<IBooks> = ({ books }) => (
    <TableContainer>
        <Table aria-label="books table">
            <TableHead>
                <TableRow>
                    <TableCell>Title</TableCell>
                    <TableCell align="right">Author</TableCell>
                    <TableCell align="right">Publication Year</TableCell>
                    <TableCell align="right">Publication Country</TableCell>
                    <TableCell align="right">Pages</TableCell>
                </TableRow>
            </TableHead>
            <TableBody>
                {books?.map((book) => (
                    <TableRow key={book.id}>
                        <TableCell component="th" scope="row">
                            {book.book_title}
                        </TableCell>
                        <TableCell align="right">{book.book_author}</TableCell>
                        <TableCell align="right">
                            {book.book_publication_year}
                        </TableCell>
                        <TableCell align="right">
                            {book.book_publication_city}
                        </TableCell>
                        <TableCell align="right">{book.book_pages}</TableCell>
                    </TableRow>
                ))}
            </TableBody>
        </Table>
    </TableContainer>
);
