import { configureStore } from '@reduxjs/toolkit';
import { TypedUseSelectorHook, useSelector, useDispatch } from 'react-redux';
import { booksApiSlice } from './api-slices/books.api.slice';
import paginationSlice from './slices/pagination.slice';
import filterReduce from './slices/filter.slice';

export const store = configureStore({
    devTools: true,
    reducer: {
        pagination: paginationSlice,
        filter: filterReduce,
        [booksApiSlice.reducerPath]: booksApiSlice.reducer
    },
    middleware: (getDefaultMiddleware) =>
        getDefaultMiddleware().concat(booksApiSlice.middleware)
});

type AppDispatch = typeof store.dispatch;
type RootState = ReturnType<typeof store.getState>;

export const useAppSelector: TypedUseSelectorHook<RootState> = useSelector;
export const useAppDispatch = () => useDispatch<AppDispatch>();
