import { InputBase, Toolbar, Typography } from '@material-ui/core';

import SearchIcon from '@material-ui/icons/Search';
import { useHistory } from 'react-router-dom';
import { setFilterValue } from '../../state/slices/filter.slice';
import { useAppDispatch, useAppSelector } from '../../state/store';
import { useToolbarStyles } from './TableToolbar.styles';

interface ITableToolbarProps {
    pageNum: number;
}

export const TableToolbar: React.FC<ITableToolbarProps> = ({ pageNum }) => {
    const classes = useToolbarStyles();
    const history = useHistory();

    const dispatch = useAppDispatch();

    const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        dispatch(setFilterValue(event.target.value));
    };

    const handleBlur = () => {
        if (pageNum !== 1) history.push(`/books/1`);
    };

    const filterValue = useAppSelector((state) => state.filter.filterValue);

    return (
        <Toolbar>
            <Typography
                className={classes.title}
                variant="h6"
                id="books"
                component="div"
            >
                Books
            </Typography>
            <div className={classes.search}>
                <div className={classes.searchIcon}>
                    <SearchIcon />
                </div>
                <InputBase
                    placeholder="Search…"
                    name="search"
                    value={filterValue}
                    onBlur={handleBlur}
                    onChange={handleChange}
                    classes={{
                        root: classes.inputRoot,
                        input: classes.inputInput
                    }}
                    inputProps={{ 'aria-label': 'search' }}
                />
            </div>
        </Toolbar>
    );
};
