import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';

export type Book = {
    id: number;
    book_author?: string[] | null;
    book_title: string;
    book_publication_year: number;
    book_publication_country: string;
    book_publication_city: string;
    book_pages: number;
};

export interface IBooks {
    books: Book[];
    count: number;
}

export interface IBooksParams {
    page?: number;
    itemsPerPage?: number;
    filterValue?: string;
}

/*
    Ideally I would have used react-query for server state management
    Since the requirement was to store the data in redux store, I used RTK-query
    I am also comfortable in using createAsyncThunk, but I believe it makes code a bit hard to read.
*/
export const booksApiSlice = createApi({
    refetchOnFocus: false,
    refetchOnReconnect: false,
    reducerPath: 'books-api',
    baseQuery: fetchBaseQuery({
        baseUrl: 'http://nyx.vima.ekt.gr:3000/'
    }),
    endpoints: (builder) => ({
        fetchBooks: builder.query<IBooks, IBooksParams>({
            query: ({ page, itemsPerPage, filterValue }) => ({
                url: `/api/books`,
                method: 'POST',
                body: {
                    page,
                    itemsPerPage,
                    filters: [{ type: 'all', values: [filterValue || ''] }]
                }
            })
        })
    })
});

export const { useFetchBooksQuery, usePrefetch } = booksApiSlice;
