import { Box, Container, Paper } from '@material-ui/core';
import { useParams } from 'react-router-dom';
import useDebouncedValue from '../../hooks/useDebouncedValue';
import { useFetchBooksQuery } from '../../state/api-slices/books.api.slice';
import { useAppSelector } from '../../state/store';
import { Loader } from '../Loader/Loader';
import { Pagination } from '../Pagination/Pagination';
import { BooksTable } from '../Table/Table';
import { TableToolbar } from '../TableToolbar/TableToolbar';
import { Toast } from '../Toast/Toast';
import { useContainerStyles } from './BooksContainer.styles';

export interface ParamTypes {
    page: string;
}

export const BooksContainer: React.FC = () => {
    const { page } = useParams<ParamTypes>();

    const classes = useContainerStyles();

    const rowsPerPage = useAppSelector((state) => state.pagination.rowsPerPage);
    const filterValue = useAppSelector((state) => state.filter.filterValue);
    const pageNum = Number(page);

    // isFetching : we have not completed the fetch
    // isLoading: subset of isFetching, we also have no cache data to display
    const { data, isLoading, isFetching } = useFetchBooksQuery({
        page: pageNum,
        itemsPerPage: rowsPerPage,
        filterValue: useDebouncedValue(filterValue, 700)
    });

    const count = data?.count;
    const totalPages = count ?? Math.ceil(count / rowsPerPage);
    const maxPageCountExceeded = pageNum > totalPages;

    if (Number.isNaN(pageNum)) {
        return (
            <Toast redirect severity="error">
                Page Param should be a number
            </Toast>
        );
    }

    if (maxPageCountExceeded) {
        return (
            <Toast redirect severity="error">
                Max allowed pages are : {totalPages - 1}
            </Toast>
        );
    }

    return (
        <>
            <Loader isLoading={isLoading || isFetching} />
            <Container maxWidth="lg">
                <Box
                    m={8}
                    display="flex"
                    flexDirection="column"
                    alignItems="center"
                >
                    <Paper className={classes.paper}>
                        <TableToolbar pageNum={pageNum} />
                        <BooksTable books={data?.books} count={count} />
                        <Pagination count={count} pageNum={pageNum} />
                    </Paper>
                </Box>
            </Container>
        </>
    );
};
